/**
 * GM_XHR
 * @typedef GMResponse
 * @property {*} response response
 * @property {number} status
 */

/**
 * GMGM.xmlHttpRequest
 * @callback GMCallback
 * @param {Object} options 
 * @returns {Promise<GMResponse>} 
 */

/**
 * GM API
 * @param {GMCallback} gmCallback 
 * @param {string} url 
 * @returns {Promise<*>} 
 */
const getDataByGM = async (gmCallback, url) => {
  const response = await gmCallback({
    method: 'get',
    url: url,
    responseType: 'blob',
  });
  if (response.status !== 200) {
    throw new Error(`GM_XHR error! status: ${response.status}`);
  }
  return response.response;
};
/**
 * fetch
 * @param {string} url 
 * @returns {Blob} 
 */
const getDataByFetch = async (url) => {
  const response = await fetch(url);
  if (!response.ok) {
    throw new Error(`HTTP error! status: ${response.status}`);
  }
  const blob = await response.blob();
  return blob;
};
/**
 * GMfetch
 * @param {string} url 
 * @param {GMCallback} callback GMGM.xmlHttpRequest
 * @returns {*} 
 */
export const getData = async (url, callback = null) => {
  if (callback === null) {
    return getDataByFetch(url);
  }
  return getDataByGM(callback, url);
};
